export default {
    data() {
        return {
            parentMode: null,
            items: [],
            activeItem: 0,
            sections: [],
            settings: {
                direction: 'x',
                reverse: true
            },
        }
    },

    mounted() {
        this.init();
        document.addEventListener('mousedown', this.mouseDown)

        document.addEventListener('resize', () => this.divideContainer(this.items))
    },

    beforeDestroy() {
        document.removeEventListener('mousedown', this.mouseDown)
        document.removeEventListener('resize', () => this.divideContainer(this.items))
    },

    methods: {
        mouseDown() {
            document.addEventListener('dragstart', e => {
                e.preventDefault()
            })
            window.addEventListener('mousemove', this.move);
            window.addEventListener('mouseup', this.mouseUp);
        },

        mouseUp() {
            window.removeEventListener('mousemove', this.move);
            window.removeEventListener('mouseup', this.mouseUp);
        },

        init() {
            this.parentMode = this.$el;
            this.items = [...this.parentMode.children];
            this.items[0].classList.add("active");
            this.divideContainer(this.items)
            this.appendImages(this.items)
        },

        appendImages(array) {
            array.forEach(i => {
                const img = document.createElement("img");
                img.src =  i.dataset.imgSrc;
                i.appendChild(img);
            })
        },

        applyClasses(sections, position) {
            sections.forEach(s => {
                if (position >= s.min && position <= s.max) {
                    this.items.forEach(i => {
                        i.classList.remove("active")
                    })
                    this.items[s.index].classList.add("active");
                }
            })
        },

        move(e) {
            // const offset = this.$el;
            let position;
            if (this.settings.direction === 'y') {
                // position = e.pageY - offset.top;
            } else {
                // position = e.pageX - offset.left;
                position = e.pageX;
            }
            this.applyClasses(this.sections, position);
        },

        divideContainer(images) {
            let initialLength,
                dividend = images.length;
            if (this.settings.direction === 'y') {
                initialLength = this.parentMode.getBoundingClientRect().height;
            } else {
                initialLength = this.parentMode.getBoundingClientRect().width;
            }

            const sectionLength = initialLength / dividend;

            // creates array of value pairs with min and max ranges
            images.forEach((img, ind) => {
                this.sections[ind] = {
                    min: sectionLength * ind,
                    max: sectionLength + (sectionLength * ind),
                    index: ind
                };
            })

            // reverses direction
            if (this.settings?.reverse === true) {
                // reverse array
                this.sections.reverse();
                // reset index values
                this.sections.forEach((s, i) => {
                    s.index = i
                })
            }
        }
    },
}